<template>
  <div>
    <h2>添加账号</h2>
    <p>
      <label>账号</label>
      <el-input v-model="newacc"
                clearable>
      </el-input>
    </p>
    <p>
      <label>密码</label>
      <el-input v-model="newpassword"
                show-password
                clearable>
      </el-input>
    </p>
    <p>
      <label>用户组</label>
      <el-select v-model="value"
                 placeholder="请选择用户组">
        <el-option v-for="item in products"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
          <span style="float: left">{{ item.label }}</span>
          <span style="float: right; color: #8492a6; font-size: 13px">{{ item.value }}</span>
        </el-option>
      </el-select>
    </p>
    <p>
      <label></label>
      <el-button type="primary"
                 @click="adduser()">添加</el-button>
      <el-button type="primary"
                 plain
                 @click="reset()">重置</el-button>
    </p>

  </div>
</template>

<script>
import { useradd } from '@/API/apis'
export default {
  data () {
    return {
      newacc: '',
      newpassword: '',

      products: [
        {
          value: '超级管理员',
          label: '超级管理员'
        },
        {
          value: '普通管理员',
          label: '普通管理员'
        },
      ],
      value: ''
    }
  },
  methods: {
    adduser () {
      useradd(this.newacc, this.newpassword, this.value).then(res => {
        if (res.data.code == 0) {
          this.$message({
            message: res.data.msg,
            type: 'success'
          });
          this.newacc = '',
            this.newpassword = '',
            this.value = ''
        }
      })
    },
    reset () {
      this.usernameval = '',
        this.passwordval = '',
        this.value = ''
    }
  }

}
</script>

<style lang="less" scoped>
p {
  padding-bottom: 25px;
  .el-input,
  .el-select {
    width: 20%;
  }
  label {
    display: inline-block;
    width: 60px;
    margin-right: 20px;
    color: #808080;
    text-align: right;
  }
}
</style>